import {Fragment, useEffect, useState} from "react";

import {useRecoilState} from "recoil";
import {animNavLoop, currentObj, navigation, sectionApp, sections} from "../../states/globalStates";
import {gsapAnimations} from "../Script";

const img1 = 'https://bodies-app.s3.amazonaws.com/3dquality/images/1.png'
const img2 = 'https://bodies-app.s3.amazonaws.com/3dquality/images/2.png'
const img3 = 'https://bodies-app.s3.amazonaws.com/3dquality/images/3.png'
const img5 = 'https://bodies-app.s3.amazonaws.com/3dquality/images/5.png'
const img6 = 'https://bodies-app.s3.amazonaws.com/3dquality/images/6.png'

const arrayButtonsSide = [
  {
    id: 1,
    title: 'frontCamera',
    name: 'Inicio',
    img: img6
  },
  {
    id: 2,
    title: 'neck',
    name: 'Cuello',
    img: img1
  },
  {
    id: 3,
    title: 'sleeve',
    name: 'Puño',
    img: img2
  },
  {
    id: 4,
    title: 'topClothe',
    name: 'Perilla',
    img: img3
  },
  {
    id: 5,
    title: 'bottomEdge',
    name: 'Ruedo',
    img: img5
  }
]


export const SideBarMain = () =>{



  const [cam,setCam] = useRecoilState(sectionApp);
  const [nav,setNav] = useRecoilState(navigation);

  const [current,setCurrent] = useRecoilState(currentObj);
  const [section,setSection] = useRecoilState(sections);

  const handleClick = (btn) =>{
    setCam(btn.title)
    setNav(btn.id)
    gsapAnimations(btn.title)
    filter(btn.title)
  }

  const filter = (id) =>{
    const currentFind = section.find(sec => sec.id === id )
    setCurrent(currentFind)
  }


  return(
    <Fragment>

      <div id="sidebar">
        {
          arrayButtonsSide.map((btn, i)=>{
            return(
              <div className="social socialLogo" key={i}>
                <a
                  onClick={()=> handleClick(btn)}
                  className="noDec text-white" >
                  <p className="mt-2 lead fw-bold">
                    {
                      btn.name
                    }
                    <i>
                      <img src={btn.img} className="imgSideBar" alt=""/>
                    </i>
                  </p>
                </a>
              </div>
            )
          })
        }
      </div>
    </Fragment>
  )
}