import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import {GLTFLoader} from 'three/examples/jsm/loaders/GLTFLoader.js'
import {RGBELoader} from 'three/examples/jsm/loaders/RGBELoader.js'
//// dat gui
import * as dat from 'dat.gui'
///gsap
import {gsap, Power2} from "gsap"
import {animationsParams} from "./Animations";

// route public models 3d

//const urlPublic = './models3D/boxer/boxer.gltf' ;
//const urlPublic = './models3D/polo/polo.gltf';
const urlPublic = 'https://bodies-app.s3.amazonaws.com/models3D/shirt/shirt.gltf';
//const urlPublic = './model3D/shirt.gltf';
//Global variables
let currentRef = null;

let setLoading = null

const timeline = new gsap.timeline({
    defaults: {
        duration: 0.8,
        ease: Power2.easeOut,
    }
})

////// tracking camera
const floatPoints = [];

//const gui = new dat.GUI({width: 600})  // controls


//Scene, camera, renderer
const scene = new THREE.Scene();
scene.background = new THREE.Color( 0xeaeaea );


const camera = new THREE.PerspectiveCamera(7, 100 / 100, 0.1, 100);
scene.add(camera);
camera.position.set(0, -1, 20);
camera.lookAt(new THREE.Vector3());

//// renderer
const renderer = new THREE.WebGLRenderer();
renderer.setSize(100, 100);


/// mejorar la textura del modelo

renderer.outputEncoding = THREE.sRGBEncoding
renderer.toneMapping = THREE.ACESFilmicToneMapping
renderer.physicallyCorrectLights = true
renderer.toneMappingExposure = 1.7

/////7

//OrbitControls
const orbitControls = new OrbitControls(camera, renderer.domElement);
orbitControls.enableDamping = true;

//Resize canvas
const resize = () => {
    renderer.setSize(currentRef.clientWidth, currentRef.clientHeight);
    camera.aspect = currentRef.clientWidth / currentRef.clientHeight;
    camera.updateProjectionMatrix();
};
window.addEventListener("resize", resize);

//Animate the scene
const animate = () => {

    for(const floatPoint of floatPoints){
        const screenPositions = floatPoint.position.clone();
        screenPositions.project(camera)
        const positionX = screenPositions.x * currentRef.clientWidth * 10;
        const positionY = screenPositions.y * -currentRef.clientHeight * 10;
       // floatPoint.element.style.transform = `translate(${positionX}px, ${positionY}px)`;

    }

    orbitControls.update();
    renderer.render(scene, camera);

    requestAnimationFrame(animate);
};
animate();

//load model 3D
const gltfLoader = new GLTFLoader()

gltfLoader.load(urlPublic,
    (gltf) =>{
        if(gltf.scene.children.length){
            scene.add( gltf.scene );
            setLoading = true
            /*const box = new THREE.Box3( ).setFromObject( gltf.scene );
            const c = box.getCenter( new THREE.Vector3( ) );
            gltf.scene.position.set( -c.x, - c.y, -c.z ); // center the gltf scene*/
        }
    },
    ()=>{

    },
    ()=>{
        console.log("Error")
    }
)


/// lights

const light1 = new THREE.DirectionalLight(0x616161, 0.8)
light1.position.set(0, 6, 1)

scene.add(light1)

const ambientLight = new THREE.AmbientLight(0x4287f5,0.8)
scene.add(ambientLight)

const light = new THREE.SpotLight(0xffa95c,4);
light.position.set(-50,50,50);
light.castShadow = true;
scene.add( light );

const envMap = new THREE.CubeTextureLoader().load(
  [
      './envmap/px.png',
      './envmap/nx.png',
      './envmap/py.png',
      './envmap/ny.png',
      './envmap/pz.png',
      './envmap/nz.png',
  ]
)
scene.environment = envMap

/// directional light

///


//////

//Init and mount the scene
export const initScene = (mountRef) => {
    currentRef = mountRef.current;
    resize();
    currentRef.appendChild(renderer.domElement);
};

//Dismount and clena up the buffer from the scene
export const cleanUpScene = () => {
    //gui.destroy();
    scene.dispose();
    currentRef.removeChild(renderer.domElement);
};

