import { useEffect, useRef } from "react";
import {ContainerScene, FloatPoint, FloatPointLabel, FloatPointText} from "./Scene.elements";
import {cleanUpScene, fetchFloatPointsElements, gsapAnimations, gsapAnimationTarget, initScene} from "./Script";
import {useRecoilState} from "recoil";
import {isLoading, isMaximize, sectionApp} from "../states/globalStates";
import {StickyBottom} from "./stickyNavs/StickyBottom";

const Scene = () => {
    const mountRef = useRef(null);
    const [cam,setCam] = useRecoilState(sectionApp);
    const [isMax, setIsMax] = useRecoilState(isMaximize)
    const [isLoad,setIsLoad] = useRecoilState(isLoading);

    useEffect(() => {
        initScene(mountRef);
        fetchFloatPointsElements()
        return () => {
            cleanUpScene();
        };
    }, []);

    useEffect(() => {
        initScene(mountRef);
    }, [isMax]);

    return (
        <>
            <ContainerScene
                isMax={isMax}
                className='SceneContainer'
                ref={mountRef}>
            </ContainerScene>

        </>
    );
};

export default Scene;