import {Fragment,useEffect, useState} from "react";

import {ModalHelp} from "../mainDash/ModalHelp";
import {useRecoilState} from "recoil";
import {isMaximize} from "../../states/globalStates";

const back =  'https://bodies-app.s3.amazonaws.com/3dquality/images/back.png'
const help =  'https://bodies-app.s3.amazonaws.com/3dquality/images/help.png'


export const StickyBottom = ()=>{
  const [modalShow, setModalShow] = useState(false);
  const [isMax, setIsMax] = useRecoilState(isMaximize)

  const animationClickTwo = () =>{
    window.location.href='https://sites.google.com/misena.edu.co/ivcs/productos/camiseta-tipo-polo?authuser=2'
  }

  return(
      <Fragment>

            <div className="btnBackRight">

              <button
                onClick={(e) => setModalShow(!modalShow)}
                className="btn">
                <img src={help}  className="logoHelp" alt=""/>
              </button>

              {
                !isMax && (
                  <button
                    onClick={(e) => animationClickTwo(e)}
                    className="btn ">
                    <img src={back}  className="logoBack" alt=""/>
                  </button>
                )
              }

            </div>

        <ModalHelp
          show={modalShow}
          onHide={() => setModalShow(false)}
        />
      </Fragment>
  )
}