import {Fragment,useEffect} from "react";

import {useRecoilState} from "recoil";
import {isMaximize} from "../../states/globalStates";

const maximize = 'https://bodies-app.s3.amazonaws.com/3dquality/images/maximize.png'


export const ButtonMaximize = ()=>{

  const [isMax, setIsMax] = useRecoilState(isMaximize)

  const handleMaximize = () =>{
    setIsMax(!isMax)
  }

  return(
      <Fragment>
            <div className="btnMaximum">
              <img
                onClick={handleMaximize}
                src={maximize}
                className="logoMaximize pulse"
                alt=""/>
            </div>
        
      </Fragment>
  )
}